.content {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Raleway', sans-serif;
  margin-top: 30px;
  padding: 10px;
}
.content-items {
  text-align: center;
  line-height: 55px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 13px;
}
.content-items h1 {
  /* border: solid; */
  font-size: 50px;
  opacity: 0.8;
  color: hsl(24.6 95% 53.1%);
  /* color: #F05D0E; */
}
.content-items p {
  font-size: 21px;
  width: 100%;
  font-weight: 600;
  color: #363738;
}
body.dark .content-items p {
  color: white;
}
a {
  text-decoration: none;
  color: hsl(24.6 95% 53.1%);
}
.content-items img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}
.content-items a {
  text-decoration: none;
  transition: color 0.3s;
  font-size: 18px;
}
body.dark .content-items a {
  color: white;
}

body.dark .content-items button {
  width: 150px;
  padding: 8px;
  cursor: pointer;
  font-size: 21px;
  color: white;
  border-radius: 50px;
  border: solid orange 1px;
  background-color: hsl(24.6 95% 53.1%);
  transition: all 1s ease;
}
body.dark .content-items button:hover {
  background: hsl(24.6, 95%, 53.1%);
  color: black;
  transform: translateY(-5px);
}

body.light .content-items button {
  width: 150px;
  padding: 8px;
  cursor: pointer;
  font-size: 21px;
  color: white;
  border-radius: 50px;
  border: solid orange 1px;
  background-color: hsl(24.6 95% 53.1%);
  transition: all 1s ease;
}
body.light .content-items a {
  color: white;
  /* color: hsl(24.6, 95%, 53.1%); */
}
body.light .content-items button:hover {
  background: hsl(25, 72%, 58%);
  color: white;
  transform: translateY(-5px);
}

body.light .content-items a:hover {
  color: white;
}

.content-icon {
  font-size: 13vh;
  color: hsl(24.6 95% 53.1%);
}

button {
  color: white;
}

@media screen and (max-width: 400px) {
  .content-items {
    text-align: center;
    line-height: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 13px;
    /* border: solid; */
    margin-top: 70px;
  }
  .content-items h1 {
    font-size: 45px;
    opacity: 0.8;
    color: hsl(25, 96%, 54%);
  }
  .content-items p {
    font-size: 15px;
    width: 100%;
    opacity: 0.9;
    font-weight: 500;
  }
  a {
    text-decoration: none;
  }
  .content-icon {
    font-size: 15vh;
    opacity: 0.8;
    color: hsl(24.6 95% 53.1%);
  }
  .content-items .content-items button {
    width: 180px;
    padding: 8px;
    cursor: pointer;
    font-size: 21px;
    color: white;
    border-radius: 50px;
    border: solid orange 1px;
    background-color: hsl(24.6 95% 53.1%);
  }
}
