.modalContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  z-index: 1000;
  align-items: center;
}

.modal {
  margin: 10px;
  background-color: #fff;
  border-radius: 14px;
  width: 100%;
  max-width: 770px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  /* border: solid red; */
  height: 95vh;
}
body.dark .modal {
  background-color: #222222;
  color: white;
}
.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  gap: 10px;
}

.searchWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f0f0f0;
  border-radius: 20px;
  padding: 5px 10px;
  flex: 2;
}

.searchIcon {
  font-size: 20px;
  color: #333;
  margin-right: 8px;
}
body.dark .searchIcon {
  color: hsl(24.6 95% 53.1%);
}
input {
  border: none;
  background: none;
  outline: none;
  font-size: 16px;
  flex: 3;
  padding: 5px;
}
body.dark .searchWrapper {
  background-color: #3e3e3e;
  color: white;
}
body.dark input {
  color: white;
}
button {
  padding: 8px 15px;
  color: hsl(24.6 95% 53.1%);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: white;
}
body.dark button {
  background-color: #222222;
}
.searchedDataContainer {
  max-height: 550px;
  overflow-y: scroll;
  color: black;
}
body.dark .searchedDataContainer {
  color: white;
}
.searchedDataContainer::-webkit-scrollbar {
  display: none;
}

.searchedDataContainer ul {
  list-style: none;
}

.searchedItem {
  padding: 10px;
  border-bottom: 1px solid #f0f0f0;
  cursor: pointer;
  color: black;
}
@media screen and (max-width: 400px) {
  .searchedDataContainer {
    height: 370px;
  }
  .searchedDataContainer::-webkit-scrollbar {
    display: none;
  }
  .modal {
    margin: 5px;
    width: 100%;
    /* border: solid red;  */
    height: 100vh;
    border-radius: 0px;
  }
  .searchIcon {
    width: 35px;
    height: 4vh;
    color: hsl(24.6 95% 53.1%);
    /* margin-left: 96px; */
    cursor: pointer;
  }
}
