  margin-top: 30px;
  padding: 20px;
  border-radius: 10px;
  font-family:
    Menlo,
    Consolas,
    Monaco,
    Liberation Mono,
    Lucida Console,
    monospace;
  color: black;
  box-shadow:
    rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

body.dark .code-example pre {
  font-size: 13.6px;
  line-height: 24px;
  background-color: #525151;
  margin-top: 30px;
  padding: 20px;
  border-radius: 10px;
  font-family:
    Menlo,
    Consolas,
    Monaco,
    Liberation Mono,
    Lucida Console,
